* {
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  font-family: inherit;
  text-decoration: none;
}

html { box-sizing: border-box; }

body {
  max-width: 1200px;
  min-height: 2416px;
  margin: 0 auto;
  padding: 0;
  font-family: 'Roboto', 'Arial', sans-serif;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  /*background-color: #91a79a;*/
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

.container {
  width: 1146px;
  padding: 0 27px;
}

.btn {
  display: inline-block;
  vertical-align: top;
  color: #f7f6f3;
  border: none;
  outline: none;
  cursor: pointer;
}

.btn:hover {
  color: #f7f6f3;
}

.btn:disabled,
.btn.disabled {
  cursor: default;
  opacity: 0.4;
}

.btn-warning {
  background: linear-gradient(0deg, #cb3926, #f16643);
  box-shadow: 0 1px 2px 0 rgba(85, 5, 2, 0.5);
}

.btn-warning:hover {
  background: linear-gradient(0deg, #ec6f5e, #f58669);
  box-shadow: 0 1px 2px 0 rgba(85, 5, 2, 0.5);
}

.btn-warning:active {
  background: linear-gradient(0deg, #e1613e, #e1613e);
  box-shadow: inset 0 1px 2px 0px rgba(85, 5, 2, 0.5);
}

/* Слайдер */

.slider {
  width: 100%;
  min-height: 100%;
  background-color: #91a79a;
}

.slider-label {
  display: none;
}

.slider-input:checked ^ .slider-label {
  display: block;
}

.btn-header-big {
  position: absolute;
  top: 541px;
  left: calc(50% - 140px);
  padding: 0 46px;
  font-size: 31.48px;
  line-height: 62.93px;
  border-radius: 32px;
  color: #fff;
}

/* Стили хедер */

.page-header {
  position: relative;
  min-height: 115px;
  margin-bottom: 528px;
  padding-top: 21px;
}

.header-logo {
  float: left;
  width: 154px;
  height: 64px;
  margin-left: -6px;
  margin-top: -12px;
}

.nav-menu {
  float: left;
  width: 400px;
  margin-left: 16px;
  padding-bottom: 8px;
}

.nav-menu ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav-menu a {
  display: block;
  padding: 8px;
  padding-left: 14px;
  padding-right: 14px;
  color: #fff;
}

.dropdown {
  position: relative;
  display: inline-block;
  vertical-align: top;
  float: left;
  margin-left: 0px;
  vertical-align: top;
}

.dropdown a:hover {
  background-color: #f7f6f3;
  border-radius: 16px;
  color: #000;
}

.dropdown:hover .dropdown-menu {
  display: block;
}

.dropdown-menu {
  display: block;
  position: absolute;
  top: 37px;
  left: -7px;
  z-index: 1;
  width: 143px;
  background-color: #f7f6f3;
  border-radius: 6px;
  box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

.dropdown-menu li:first-child {
  position: relative;
}

.dropdown-menu li:first-child::after {
  content: '';
  position: absolute;
  top: 35px;
  width: 90%;
  margin-left: 5px;
  border-bottom: 1px solid #d1d0ce;
}

.dropdown-menu a {
  padding: 10px;
  padding-left: 20px;
  padding-right: 5px;
  font-size: 14px;
  color: #000;
}

.dropdown-menu a:hover {
  background-color: #fbded7;
  border-radius: 0;
}

.dropdown-menu a:active {
  background-color: #f6b5a5;
}

.dropdown-menu a: {
  background-color: #d07058;
}

.dropdown-menu li:first-child a:hover,
.dropdown-menu li:first-child a:active {
  background-color: #f7f6f3;
}

.dropdown-menu a:nth-child(n+1) {
  font-weight: 400;
}

/* форма Поиска */

.search {
  position: relative;
  float: left;
  width: 330px;
  margin-left: 23px;
  padding-bottom: 8px;
}

.btn-search {
  position: relative;
  display: block;
  width: 32px;
  height: 32px;
  float: right;
  vertical-align: top;
  background-color: rgba(248, 247, 244, 0.2);
  border-radius: 50%;
}

.btn-search::before {
  content: '';
  position: absolute;
  top: 6px;
  left: 6px;
  width: 20px;
  height: 20px;
  background-image: url(../img/sprite.png);
  background-position: -4px -1px;
}

.btn-search:hover,
.btn-enter:hover,
.btn-cart:hover {
  background-color: rgba(248, 247, 244, 1.0);
  color: #000;
}

.btn-search:hover::before {
  background-position: -4px -28px;
}

.header-search {
  display: none;
}

.btn-search:hover + .header-search {
  position: absolute;
  display: block;
  top: 36px;
  left: -12px;
  width: 312px;
  padding: 15px;
  background-color: #f8f7f4;
  border-radius: 6px;
  -webkit-box-shadow: 0 10px 20px 0 rgba(0, 0, 0, .3);
  box-shadow: 0 10px 20px 0 rgba(0, 0, 0, .3);
}

.search-form {
  position: relative;
}

.search-form-input {
  width: 310px;
  padding: 8px 12px;
  font-weight: 400;
  line-height: 24.3px;
  border: 1px solid rgb(199, 198, 197);
  border-radius: 3px;
}

.search-form-input:hover {
  padding: 7px 11px;
  border: 2px solid rgb(199, 198, 197);
}

.search-form .form-label {
  display: block;
  position: absolute;
  top: 8px;
  left: 15px;
  width: 130px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24.3px;
  color: #9b9b9b;
}

.search-form-input:focus + .form-label {
  display: block;
  position: absolute;
  top: -18px;
  left: 15px;
  width: 130px;
  font-size: 11px;
  font-weight: 400;
  color: #8fbdec;
}

/* конец Поиск */
/* вход и корзина */

.user-block {
  position: relative;
  float: right;
  width: 220px;
  padding-bottom: 8px;
}

.btn-enter,
.btn-cart {
  position: relative;
  padding: 8px;
  font-size: 14px;
  font-weight: 500;
  background-color: rgba(248, 247, 244, 0.2);
  border-radius: 16px;
}

.btn-enter {
  float: left;
  padding: 8px;
  padding-left: 45px;
  padding-right: 16px;
}

.btn-enter::before,
.btn-cart::before {
  content: '';
  position: absolute;
  top: 6px;
  left: 15px;
  width: 20px;
  height: 20px;
}

.btn-enter::before {
  background-image: url(../img/sprite.png);
  background-position: -6px -58px;
}

.btn-enter:hover::before {
  background-position: -7px -91px;
}

.header-enter {
  display: none;
}

.btn-enter:hover + .header-enter {
  position: absolute;
  display: block;
  top: 36px;
  left: -180px;
  width: 239px;
  padding: 22px 16px 15px;
  background-color: rgb(248, 247, 244);
  border-radius: 6px;
  box-shadow: 0 10px 20px 0 rgba(0, 0, 0, .3);
}

.header-enter form {
  display: block;
}

.header-enter-input,
.header-enter-input {
  position: relative;
  width: 213px;
  margin-bottom: 12px;
  padding: 9px 12px;
  border: 1px solid rgb(199, 198, 197);
  border-radius: 3px;
  color: #999;
}

.header-enter-input:hover,
.header-enter-input:hover {
  padding: 8px 11px;
  border: 2px solid rgb(199, 198, 197);
}

.header-enter-input[type="email"] + label,
.header-enter-input[type="password"] + label {
    position: absolute;
    left: 29px;
    width: 160px;
    font-size: 16px;
    font-weight: 400;
    line-height: 24.3px;
    color: #9b9b9b;
}

.header-enter-input[type="email"] + label {
  top: 29px;
}

.header-enter-input[type="password"] + label {
  top: 77px;
}

.header-enter-input[type="email"]:focus + label,
.header-enter-input[type="password"]:focus + label {
    position: absolute;
    left: 29px;
    width: 160px;
    font-size: 11px;
    font-weight: 400;
    color: #8fbdec;
}

.header-enter-input[type="email"]:focus + label {
  top: 4px;
}

.header-enter-input[type="password"]:focus + label {
  top: 54px;
}

.btn-enter-form {
  float: left;
  margin-top: 4px;
  margin-right: 19px;
  padding: 10px 23px;
  border-radius: 18px;
}

.password,
.register {
  font-size: 13px;
  font-weight: 400;
  line-height: 23.4px;
  color: #000;
  text-decoration: underline;
}

.password:hover,
.register:hover {
  text-decoration: none;
}

.btn-cart {
  float: right;
  padding: 8px;
  padding-right: 35px;
  padding-left: 44px;
}

.btn-cart::before {
  background-image: url(../img/sprite.png);
  background-position: -6px -121px;
}

.btn-cart:hover::before {
  background-position: -7px -152px;
}

.header-cart {
  display: none;
}

/* расписание работы */

.schedule {
  float: right;
  width: 173px;
  margin-top: 18px;
}

.mode {
  margin: 0;
  font-size: 14px;
  text-indent: 18px;
}

.phone {
  font-size: 22px;
  font-weight: 900;
}

/* Конец хедер */
/* баннеры */

.selection {
  margin-bottom: 40px;
}

.selection img {

}

.selection-left,
.selection-right {
  position: relative;
  width: 560px;
  height: 229px;
  background-color: #482d21;
}

.selection-left {
  float: left;
}

.selection-right {
  float: right;
}

.selection-item {
  position: relative;
}

.selection-wrapper {
  position: absolute;
  z-index: 10;
  top: 17px;
  left: 18px;
}

.selection h2,
.selection p {
  margin: 0;
  padding: 0;
}

.selection p {
  width: 530px;
  margin-top: 17px;
  letter-spacing: -0.1px;
}

.selection-right p {
  letter-spacing: -0.8px;
}

.selection h2 {
  font-size: 35px;
}

.selection p,
.btn-middle {
  font-size: 18px;
}

.btn-middle {
  position: absolute;
  bottom: 25px;
  right: 20px;
  padding: 13px;
  padding-left: 23px;
  padding-right: 23px;
  border-radius: 22px;
}

/* Хиты */

.index {
  margin-bottom: 12px;
}

.index-creme {
  position: relative;
  float: left;
  width: 267px;
  height: 350px;
  margin-right: 26px;
}

.index-creme:last-child {
  margin-right: 0;
}

.index-img {
  width: 267px;
  height: 267px;
}

.index-creme::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 62px;
  height: 62px;
  background-image: url(../img/sprite.png);
  background-position: -94px -7px;
}

.index-price {
  position: absolute;
  top: 208px;
  left: 0;
  font-size: 43px;
}

.index-price::after {
  content: '';
  position: absolute;
  top: 8px;
  left: 78px;
  width: 26px;
  height: 34px;
  background-image: url(../img/sprite.png);
  background-position: -45px -93px;
}

.index-weight {
  position: absolute;
  top: 220px;
  left: 105px;
  font-size: 30px;
}

.index-title {
  display: block;
  margin: 9px auto;
  line-height: 22px;
  text-align: center;
  color: #f7f6f3;
}

/* конец Хит */
/* О магазине */

.features {
  width: 1146px;
  height: 281px;
  margin: 0 auto;
  margin-bottom: 40px;
  padding-top: 25px;
  background: #eaddc1 url(../img/wafer.png);
  border-radius: 17px;
  font-weight: 400;
  line-height: 22px;
  color: #000;
}

.features p:first-child {
  margin: 0;
}

.features b {
  display: block;
  margin-left: 20px;
  font-size: 24px;
  font-weight: 500;
  line-height: 30px;
  letter-spacing: 0.1px;
}

.features-index {
  position: relative;
  display: block;
  float: left;
  width: 482px;
  margin: 20px 7px 2px 77px;
  line-height: 22px;
}

.features-index:nth-child(2n+1) {
  margin-right: 0;
}

.features-index::before {
  content: '';
  position: absolute;
  top: -13px;
  left: -57px;
  width: 49px;
  height: 49px;
  background-image: url(../img/sprite.png);
}

.features-index-1::before {
  background-position: -100px -80px;
}

.features-index-2::before {
  background-position: -164px -140px;
}

.features-index-3::before {
  background-position: -100px -140px;
}

.features-index-4::before {
  background-position: -40px -140px;
}

/* Конец о магазине */
/* Блог и Подписка */

.news {
  margin-bottom: 40px;
}

.news-left,
.news-right {
  width: 560px;
  height: 220px;
  background-color: #fff;
  border-radius: 14px;
  color: #000;
}

.news-left {
  float: left;
  background-image: url(../img/blog-back.png);
}

.news-left span {
  display: block;
  margin-left: 20px;
  margin-top: 25px;
}

.news-left a {
  display: block;
  margin-left: 20px;
  margin-top: 8px;
  font-size: 24px;
  line-height: 28px;
  text-decoration: underline;
  color: #000;
}

.news-right {
  float: right;
  background-image: url(../img/post-back.png);
}

.news-right-wrapper {
  width: 550px;
  height: 209px;
  margin: 5px;
  background-color: #f8f7f4;
  border-top: 1px solid transparent;
  border-radius: 10px;
}

.news-right p {
  display: block;
  margin: 30px 22px 35px;
  font-weight: 400;
  line-height: 22px;
  color: #8e8e8d;
}

.delivery {
  position: relative;
  margin: 0 20px;
}

.news-input {
  width: 350px;
  padding: 8px;
  font-weight: 400;
  line-height: 24.3px;
  border: 1px solid #d3d3d2;
  border-radius: 3px;
}

.news-input:hover {
  padding: 7px;
  border: 2px solid #d3d3d2;
}

.news-input-label {
  position: absolute;
  top: 9px;
  left: 9px;
  width: 150px;
  font-weight: 400;
  line-height: 24.3px;
  color: #808080;
}

.news-input:focus + .news-input-label {
  position: absolute;
  top: -19px;
  left: 9px;
  width: 150px;
  font-size: 11px;
  font-weight: 400;
  line-height: 24.3px;
  color: #8fbdec;
}

.btn-news {
  margin-left: 8px;
  padding: 13px 21px;
  border-radius: 22px;
}

/* конец Новости и Подписка */
/* Карта */

.map {
  position: relative;
  z-index: 1;
  height: 428px;
}

.yandex-map {
  position: relative;
  z-index: 2;
}

.map-form {
  position: absolute;
  z-index: 10;
  right: 27px;
  top: 66px;
  width: 277px;
  height: 290px;
  padding-left: 27px;
  padding-top: 15px;
  background-color: #fff;
  border-radius: 13px;
  color: #000;
}

.map-form p {
  display: block;
  margin-bottom: 20px;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}

.map-form span {
  font-size: 18px;
  font-weight: bold;
  line-height: 24px;
}

.btn-map {
  margin: 4px auto;
  padding: 14px 35px;
  border-radius: 22px;
  color: #fff;
}

/* конец Карта и Основная часть */
/* вложенные страницы */

.catalog-page {
  width: 1200px;
  min-height: 1600px;
  margin: 0 auto;
  padding: 0;
  background-color: #91a79a;
}

.catalog-page ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.catalog-page .page-header {
  margin-bottom: -30px;
}

.active {
  display: block;
  padding: 8px;
  padding-left: 14px;
  padding-right: 14px;
  color: #fff;
  background-color: #d07058;
  border-radius: 16px;
}

.dropdown-menu .active {
  border-radius: 0;
}

/* Пагинатор */

.main {
  padding-bottom: 72px;
  border-bottom: 1px solid rgba(248, 247, 244, .5);
}

.breadcrumbs {
  margin-bottom: 53px;
}

.breadcrumbs ul {
}

.breadcrumbs li {
  position: relative;
  display: inline-block;
  margin-right: 5px;
  padding-right: 7px;
  vertical-align: top;
  font-size: 14px;
  font-weight: 400;
}

.breadcrumbs li::after {
  content: "»" ;
  position: absolute;
  right: -5px;
  top: 0px;
}

.breadcrumbs li:last-child::after {
  display: none;
}

.breadcrumbs a {
  text-decoration: underline;
  color: inherit;
}

.breadcrumbs h1 {
  margin-top: 5px;
  font-size: 35px;
  line-height: 30px;
}

/* Фильтр */
/* Селект */

.filter-item {
  width: 885px;
}

.filter {
  position: relative;
  display: inline-block;
  vertical-align: top;
  margin-right: 15px;
  list-style: none;
}

.filter-label {
  position: absolute;
  top: -23px;
  left: 15px;
  font-size: 14px;
  font-weight: 500;
}

.filter option {
  font-size: 14px;
  font-weight: 400;
  border: none;
  outline: none;
}

.filter option {
  color: #000;
}

.filter-popular,
.filter-price,
.filter-fat,
.filter-fill {
  margin-bottom: 40px;
  font-size: 16px;
  font-weight: 500;
  color: inherit;
  border: none;
  border-radius: 18px;
  background-color: rgba(255, 255, 255, .2);
}

.filter-popular {
  width: 193px;
  padding: 8px 20px 9px 20px;
  border: none;
  outline: none;
}

.no-visible {
  display: none;
}
.filter-popular:hover,
.filter-popular:active {
  background-color: #fff;
  color: #000;
}

/* конец Селект */
/* Цены */

.filter-price {
  width: 218px;
  height: 36px;
}

.price-input,
.fat-input,
.fill-input {
  display: none;
}

.filter-price .min-price {
  content: '';
  position: absolute;
  top: 21%;
  left: 7%;
  width: 86%;
  height: 4px;
  background-color: rgba(248, 247, 244, .5);
}

.filter-price .max-price {
  content: '';
  position: absolute;
  top: 21%;
  left: 20%;
  width: 50%;
  height: 4px;
  background-color: rgba(248, 247, 244, 1);
}

.filter-price .min-price-left::before,
.filter-price .max-price-right::before {
  content: '';
  position: absolute;
  top: -8px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: rgba(248, 247, 244, 1);
}

.filter-price .min-price-left::before {
  left: 5%;
}

.filter-price .max-price-right::before {
  left: 85%;
}

.filter-price .min-price-left:hover::before,
.filter-price .max-price-right:hover::before {
  cursor: pointer;
}

/* конец Цена */
/* Жирность */

.filter-fat {
  width: 415px;
  height: 36px;
}

.filter-fat-label {
  position: relative;
  display: inline-block;
  margin-left: 44px;
  padding-top: 9px;
}

.fat-input + .filter-fatness {
  content: '';
  position: absolute;
  top: 8px;
  left: -30px;
  width: 16px;
  height: 16px;
  border: 1px solid rgba(248, 247, 244, 1);
  border-radius: 50%;
}

.fat-input:checked + .filter-fatness::before {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 12px;
  height: 12px;
  background: rgba(248, 247, 244, 1);
  border-radius: 50%;
}

.fat-input + .filter-fatness:hover {
  content: '';
  position: absolute;
  top: 8px;
  left: -30px;
  width: 16px;
  height: 16px;
  border: 2px solid rgba(248, 247, 244, 1);
  border-radius: 50%;
}

.fat-input:checked + .filter-fatness::before:hover {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 12px;
  height: 12px;
  background: rgba(248, 247, 244, 1);
  border-radius: 50%;
}

.fat-input + .filter-fatness:disabled {
  content: '';
  position: absolute;
  top: 8px;
  left: -30px;
  width: 16px;
  height: 16px;
  border: 2px solid rgba(248, 247, 244, .4);
  border-radius: 50%;
}

.fat-input:checked + .filter-fatness::before:disabled {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 12px;
  height: 12px;
  background: rgba(248, 247, 244, .4);
  border-radius: 50%;
}



/* конец Жирность */
/* Наполнители */

.filter-fill {
  width: 702px;
  height: 36px;
}

.filter-fill-label {
  position: relative;
  display: inline-block;
  margin-left: 50px;
  padding-top: 9px;
}

.fill-input + .filter-fillness {
  content: '';
  position: absolute;
  top: 8px;
  left: -30px;
  width: 16px;
  height: 16px;
  border: 1px solid rgba(248, 247, 244, .5);
  border-radius: 5px;
}

.fill-input:checked + .filter-fillness::before {
  content: '';
  position: absolute;
  top: 0;
  left: 5px;
  width: 4px;
  height: 10px;
  border: 2px solid rgba(248, 247, 244, 1);
  border-left: none;
  border-top: none;
  transform: rotate(45deg);
}

.fill-input + .filter-fillness:hover {
  content: '';
  position: absolute;
  top: 8px;
  left: -30px;
  width: 16px;
  height: 16px;
  border: 2px solid rgba(248, 247, 244, .5);
  border-radius: 5px;
}

.fill-input:checked + .filter-fillness::before:hover {
  content: '';
  position: absolute;
  top: 0;
  left: 5px;
  width: 4px;
  height: 10px;
  border: 2px solid rgba(248, 247, 244, 1);
  border-left: none;
  border-top: none;
  transform: rotate(45deg);
}

.fill-input + .filter-fillness:disabled {
  content: '';
  position: absolute;
  top: 8px;
  left: -30px;
  width: 16px;
  height: 16px;
  border: 1px solid rgba(248, 247, 244, .5);
  border-radius: 5px;
}

.fill-input:checked + .filter-fillness::before:disabled {
  content: '';
  position: absolute;
  top: 0;
  left: 5px;
  width: 4px;
  height: 10px;
  border: 2px solid rgba(248, 247, 244, 1);
  border-left: none;
  border-top: none;
  transform: rotate(45deg);
}

