/* конец Фильтры */
/* Основной контент */

.catalog-page .index {
  position: relative;
  z-index: 1;
  margin-bottom: 85px;
}

.catalog-page .index-img {
  position: relative;
  z-index: -1;
}

.catalog-page .index-price,
.catalog-page .index-weight {
  position: absolute;
  z-index: 1;
}

.inner-index-creme {
  position: relative;
  z-index: 10;
  float: left;
  width: 267px;
  height: 350px;
  margin-right: 9px;
  margin-left: 10px;
  margin-top: 25px;
}

.inner-index-visible {
  position: absolute;
  top: -10px;
  left: -9px;
  z-index: 100;
  visibility: hidden;
  width: 285px;
  height: 55px;
  padding-top: 340px;
  background-color: rgba(248, 247, 244, .3);
  border-radius: 7px;
  box-sizing: content-box;;
}

.inner-index-visible .btn-warning {
  display: block;
  margin: 0 auto;
  padding: 10px 30px;
  border-radius: 18px;

}

.inner-index-creme:hover .inner-index-visible {
  visibility: visible;
  z-index: 1000;
}

.inner-index-creme::before {
  content: none;
}

/* конец Основной контент */
/* Пагинатор */

.catalog-page .paginator {
  float: right;
  width: 220px;
  margin-bottom: 40px;
}

.paginator a,
.page-active {
  display: block;
  float: left;
  padding: 5px 10px;
  border-radius: 50%;
  color: #fff;
}

.paginator .arrow-left,
.paginator .arrow-right {
  content: '';
  margin-top: 7px;
  padding: 5px;
  border: 2px solid rgba(255, 255, 255,  1);
  border-radius: 0;
  transform: rotate(45deg);
}

.paginator .arrow-left {
  border-right: none;
  border-top: none;
}

.paginator .arrow-right {
  border-left: none;
  border-bottom: none;
}

.paginator .page-disabled {
  border-color: rgba(255, 255, 255,  .3);
}

.paginator .page-active  {
  background-color: rgba(255, 255, 255,  1);
  color: #000;
}

.paginator a:hover {
  background-color: rgba(248, 247, 244, .3);
}

.paginator a:active {
  background-color: rgba(255, 255, 255,  1);
  color: #000;
}

.paginator .arrow-right:hover,
.paginator .arrow-left:hover {
  background-color: transparent;
}

