/* Всплывающая форма обратной связи. */

.common {
  display: none;
  position: absolute;
  top: 25%;
  left: 25%;
  width: 430px;
  padding: 19px 24px;
  background-color: rgb(248,247,244);
  border-radius: 10px;
}

.common-exit {
  position: absolute;
  text-outline: 10px;
  right: 20px;
  width: 20px;
  height: 20px;
  border: none;
  outline: none;
  background-color: transparent;
}

.common-exit::before,
.common-exit::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 20px;
  height: 2px;
  background-color: rgba(0,0,0,.5);
}

.common-exit::before {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.common-exit::after {
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

